.card {
    &-book {
	position: relative;
    display: flex;
	padding: 0rem 0.5rem 0rem 1rem;
	margin-block-end: 3rem;
	background-color: var(--clr-main-700);
    border-radius: 150px 5px 5px 200px;
        &-right {
            display: flex;
            flex-direction: column;
            width: 100%;
            text-align: center;
        }
        //------animation in-out
        &-enter {
            opacity: 0.1;
            transform-origin: top right;
            transform: scale(0.45);

            &-active {
                opacity: 1;
                transform-origin: top right;
                transform: scale(1);
                transition: opacity 200ms ease-in-out 200ms, transform 400ms ease-in-out;
            }
        }
        &-exit {
            opacity: 1;
            transform-origin: top right;
            transform: scale(1);

            &-active {
                opacity: 0.1;
                transform-origin: top right;
                transform: scale(.50);
                transition: opacity 400ms ease-in-out, transform 500ms ease-in-out;
            }
        }
    }
    &-control-menu {
        display: flex;
        flex-direction: column;
        width: 150px;
        margin-block-end: 2rem;
        border-radius: 10px;
        isolation: isolate;

        & .input-btn-group {
            display: flex;
            flex-direction: column;
        }
    }
    &-sort-option {
        position: relative;
        border-bottom-right-radius: 10px;
        border-bottom-left-radius: 10px;
        background-color: var(--clr-main-680);
        margin-block-end: -8px;
        &.last-option {
            border-radius: 10px;
        }
        &.sub {
            margin-block-end: 0;
            margin-block-start: -8px;
            background-color: var(--clr-accent-dark);
        }
        &.bg-main-900 {
            @media (max-width: 650px) {
                background-color: transparent;
            }
        }
    }
    &-found-book {
        border-radius: 150px 5px 5px 200px;
        padding: 1rem;
        margin-block-start: 4rem;
        display: flex;
        align-items: flex-start;
        flex: 1;
        width: 100%;
        background: linear-gradient(
		rgba(var(--clr-rgb-main-700), 1) 0%,
		rgba(var(--clr-rgb-main-700), 1) 55%,
		rgba(var(--clr-rgb-main-900), 1) 90%,
		rgba(var(--clr-rgb-main-900), 1) 100%
	);
    @media (max-width: 650px) {
        // width: calc(100% + 6rem);
      }
    }
}